// src/components/Gem/Gem.test.tsx
import React from 'react'
import {render, fireEvent} from '@testing-library/react-native'
import {Gem} from '../'
import {ICONS} from './images'

console.log(ICONS)

describe('Gem Component', () => {
  test('should render the correct image based on planNumber prop', () => {
    const {getByTestId} = render(<Gem planNumber={3} onPress={() => {}} />)
    const gemImage = getByTestId('gem-image')
    expect(gemImage.props.source).toEqual({uri: ICONS[3]}) // Check the correct image based on planNumber prop
  })

  test('should render the default image if player prop is missing', () => {
    const {getByTestId} = render(<Gem planNumber={10} onPress={() => {}} />)
    const gemImage = getByTestId('gem-image')
    console.log('gemImage.props', gemImage.props)
    expect(gemImage.props.source).toEqual({uri: ICONS[0]}) // Check the default image if player prop is missing
  })

  test('should call the onPress function when pressed', () => {
    const onPressMock = jest.fn()
    const {getByTestId} = render(<Gem planNumber={5} onPress={onPressMock} />)
    const gemContainer = getByTestId('gem-container')
    fireEvent.press(gemContainer)
    expect(onPressMock).toHaveBeenCalled()
  })
})
